@use 'base/variables' as v;
@use 'base/mixins' as m;


.navegacion {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin-top: 1rem;
}
@media (min-width: 768px) { 
    .navegacion {
        flex-direction: row;
    }
}
.navegacion a {
    padding:  .5rem;
    transition: background-color .3s;
    width: 100%;
    text-align: center;
}
@media (min-width: 768px) { 
    .navegacio a {
        padding:  1rem 2rem;
    }
}
.navegacion a:hover {
    background-color: rgb(255 255 255 / .5);
    color: v.$negro;
}