body {
  background-color: #f8f8f8;
  font-family: "Raleway", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  font-size: 2rem;
}

[class$=contenedor],
.contenedor {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.seccion {
  padding: 8rem 0;
}

/** Utilidades **/
.text-center {
  text-align: center;
}

.mt-5 {
  margin-top: 5rem;
}

h1, h2, h3 {
  font-weight: 900;
  line-height: 1.2;
  margin: 1rem 0 4rem 0;
}

h1 {
  font-size: 4.8rem;
}

h2 {
  font-size: 5.8rem;
}

h3 {
  font-size: 5rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
}

.btn {
  background-color: #FFFFFF;
  padding: 2rem 2rem;
  color: #343a40;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
}
@media (min-width: 768px) {
  .btn {
    width: auto;
  }
}
.btn:hover {
  background-color: #e6e6e6;
}

.boton-negro {
  background-color: #111111;
  padding: 2rem 2rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
}
@media (min-width: 768px) {
  .boton-negro {
    width: auto;
  }
}
.boton-negro:hover {
  background-color: black;
}

.header {
  min-height: 60rem;
  background-image: url(../img/hero.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding: 5rem 0;
}

.contenido-header {
  display: flex;
  flex-direction: column;
  height: calc(60rem - 4rem);
  justify-content: space-between;
  color: #FFFFFF;
}

.nombre-sitio {
  font-weight: 300;
  text-align: center;
  font-size: 4rem;
}

@media (min-width: 768px) {
  .nombre-sitio {
    font-size: 6rem;
  }
}
.nombre-sitio span {
  font-weight: 900;
}

@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.contacto {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media (min-width: 768px) {
  .contacto {
    align-items: flex-end;
  }
}
.telefono {
  display: flex;
}

.telefono::before {
  content: "";
  display: block;
  width: 4rem;
  height: 4rem;
  background-image: url(../img/telefono.png);
  background-repeat: no-repeat;
  background-position: center center;
  margin-right: 1rem;
}

.header a {
  color: #FFFFFF;
  font-size: 2rem;
}

.header a:hover {
  background-color: rgba(255, 255, 255, 0.5);
  color: #111111;
}

.slogan {
  text-align: center;
}

@media (min-width: 768px) {
  .slogan {
    text-align: right;
  }
}
.slogan h1 {
  font-size: 3rem;
  margin: 0;
}

@media (min-width: 768px) {
  .slogan h1 {
    font-size: 5rem;
  }
}
.slogan p {
  margin: 0;
  line-height: 1;
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin-top: 1rem;
}

@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
  }
}
.navegacion a {
  padding: 0.5rem;
  transition: background-color 0.3s;
  width: 100%;
  text-align: center;
}

@media (min-width: 768px) {
  .navegacio a {
    padding: 1rem 2rem;
  }
}
.navegacion a:hover {
  background-color: rgba(255, 255, 255, 0.5);
  color: #111111;
}

.sociales {
  margin: 5rem 0;
  display: flex;
}
@media (min-width: 768px) {
  .sociales {
    margin: 0;
  }
}
.sociales__enlace::before {
  content: "";
  padding: 2rem;
  display: inline-block;
}
.sociales__enlace[href*="facebook.com"]::before {
  background-image: url(../img/icono_facebook.svg);
}
.sociales__enlace[href*="twitter.com"]::before {
  background-image: url(../img/icono_twitter.svg);
}
.sociales__enlace[href*="instagram.com"]::before {
  background-image: url(../img/icono_instagram.svg);
}
.sociales__enlace[href*="youtube.com"]::before {
  background-image: url(../img/icono_youtube.svg);
}
.sociales__enlace[href*="tiktok.com"]::before {
  background-image: url(../img/icono_tiktok.svg);
}
.sociales__accesible {
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
  display: block;
}

@media (min-width: 768px) {
  .rutas {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.imagen-rutas {
  height: 40rem;
  background-image: url(../img/rutas.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100%;
}

@media (min-width: 768px) {
  .imagen-rutas {
    height: 100%;
  }
}
.styled-table {
  border-collapse: collapse;
  margin: 25px 0;
  font-size: 0.9em;
  font-family: sans-serif;
  min-width: 400px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
  width: 100%;
}

.styled-table thead tr {
  background-color: #009879;
  color: #ffffff;
  text-align: left;
}

.styled-table th,
.styled-table td {
  padding: 5px 8px;
}

.galeria {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}

@media (min-width: 768px) {
  .galeria {
    grid-template-columns: repeat(3, 1fr);
  }
}
.galeria img {
  object-fit: cover;
  height: 100%;
}

.galeria .imagen:nth-child(2) {
  grid-column: 2/3;
  grid-row: 1/3;
}

@media (min-width: 768px) {
  .galeria .imagen:nth-child(2) {
    grid-column: 1/2;
    grid-row: 2/4;
  }
}
.galeria .imagen:nth-child(3) {
  grid-column: 2/3;
  grid-row: 3/5;
}

@media (min-width: 768px) {
  .galeria .imagen:nth-child(3) {
    grid-column: 2/3;
    grid-row: 1/2;
  }
}
.galeria .imagen:nth-child(6) {
  grid-column: 1/2;
  grid-row: 2/4;
}

@media (min-width: 768px) {
  .galeria .imagen:nth-child(6) {
    grid-column: 3/4;
    grid-row: 1/3;
  }
}
.content-box-container {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}

.content-box {
  position: relative;
  height: 30rem;
  max-height: 30rem;
  border-radius: 0.5rem;
}
.content-box img {
  border-radius: 0.5rem;
  object-fit: cover;
  height: 100%;
  width: 100%;
}
.content-box .capa-box {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.content-box .capa-box p {
  text-align: center;
  font-weight: bold;
  background-color: rgba(255, 255, 255, 0.5);
}
.content-box .capa-box p a {
  color: #111111;
}

@media (min-width: 768px) {
  .content-box-container {
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .videos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
.footer {
  height: 50rem;
  background-image: url(../img/footer_bg.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  color: #FFFFFF;
}

.footer a {
  color: #FFFFFF;
  font-size: 2rem;
}

.copyright {
  background-color: #111111;
  text-align: center;
  margin: 5rem 0 0 0;
  font-size: 2rem;
  padding: 1rem;
}