@use 'variables' as v; 

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
  
body {
    background-color: v.$grisClaro;
    font-family: v.$fuentePrincipal;
    font-size: 1.6rem;
    line-height: 1.8;
}

p {
    font-size: 2rem;
}
[class$="contenedor"],
.contenedor {
    width: 90%;
    max-width: 120rem;
    margin: 0 auto;
}
img {
    max-width: 100%;
    display: block;
}
ul {
    padding:0;
    list-style: none;
    margin: 0;
}
.seccion {
    padding: 8rem 0;
}

/** Utilidades **/ 
.text-center {
    text-align: center;
}
.mt-5 {
    margin-top: 5rem;
}