@use 'base/variables' as v;
@use 'base/mixins' as m;

.content-box-container{
    display: grid;
    grid-template-columns: repeat(1, 1fr);   
    gap: 2rem; 
}
.content-box{
    position: relative;
    height: 30rem;
    max-height: 30rem;
    border-radius: 0.5rem;
    img{
        border-radius: 0.5rem;
        object-fit: cover;
        height: 100%;
        width: 100%;
    }
    .capa-box{
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        p{
            text-align: center;
            font-weight: bold;
            background-color: rgb(255 255 255 / .5);
            a {
                color: v.$negro;
            }
        }
    }

}
@include m.tablet { 
    .content-box-container{
        grid-template-columns: repeat(3, 1fr);
        gap: 2rem;
    }   
}