@use 'base/variables' as v;
@use 'base/mixins' as m;

@media (min-width: 768px) { 
    .rutas {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        align-items: center;
    }
}

.imagen-rutas{
    height: 40rem;
    background-image: url(../img/rutas.jpg);
    background-repeat: no-repeat;
    background-position:center center;
    background-size: 100%;
}
@media (min-width: 768px) { 
    .imagen-rutas{
        height: 100%;
    }
}